import useMediaQuery from '@mui/material/useMediaQuery';
import { Channel, ChannelBox, ChannelLogo } from 'planby';

interface ChannelItemProps {
  channel: Channel;  
}

const ChannelItem = ({ channel }: ChannelItemProps) => {

  const { title, position, number, logo } = channel;
  // const xs = useMediaQuery('(min-width:420px)');
  const sm = useMediaQuery('(min-width:600px)');
  // const md = useMediaQuery('(min-width:1200px)');

  return (
    <ChannelBox {...position} style={{maxWidth:sm?"":"50px"}}>
      { sm &&
      <><h3 className="channel-number">{number}</h3>
      <ChannelLogo
          src={logo}
          alt={title}
          style={{ maxHeight: 120, maxWidth: 120 }} /></>}
        {!sm &&
        <span style={{whiteSpace:"nowrap",overflow:"hidden",textOverflow:"ellipsis",textAlign:"left"}}>{channel.title}</span>
        }
    </ChannelBox>
  );
};

export default ChannelItem;
