import { Channel, Program } from "planby";
import {v4 as uuidv4} from 'uuid';
// interfaces
import Channels from "../../../classes/Channels";
import { ChannelWithPosition } from "planby/dist/Epg/helpers/types";
import { duration } from "../../../utils";

export default function normalizeData(data: Channels) {
  

  const normalizedChannels: Channel[] = [];
  const normalizedPrograms: Program[] = [];
  let idx=0;
  for (const channel of data.getAllChannels()) {
    idx++;
    const newChannel = {
      uuid: channel.id,
      type: "channel",
      title: channel.name,
      provider: "Greek TV",
      logo: channel.iconSrc,
      number: idx,
      position: {
        top: 0,
        height: 100,
      } 
    } as unknown as ChannelWithPosition;

    normalizedChannels.push(newChannel);

    for (const event of channel.schedules.programs) {
      const newProgram = {
        id: `${idx}-${uuidv4()}`,
        channelUuid: channel.id,
        title: event.getProgramInfo().title,
        description: event.getProgramInfo().desc,
        since: event.start,
        till: event.stop,
        duration: duration(event.start,event.stop),
        image: "",
      } as unknown as Program;

      normalizedPrograms.push(newProgram);
    }
  }

  return {
    normalizedChannels,
    normalizedPrograms,
  };
}
