import React, { useEffect } from "react";
import useTVChannels from "./TVChannelsContextHook";
import { Stack, IconButton } from "@mui/material";
import {
  LiveTv,
  Dvr,
  ViewModule,
  PlayArrow,
  Pause,
  SkipNext,
  SkipPrevious,
  VolumeOff,
  VolumeUp,
  Fullscreen,
} from "@mui/icons-material";

const Navigation: React.FC = () => {
  const {
    xs,
    sm,
    isChannelsListOpen,
    isEPGOpen,
    isVROpen,
    isUserInactive,
    isVideoPaused,
    isVideoMuted,
    channels,
    setIsEPGOpen,
    setIsVROpen,
    setIsChannelsListOpen,
    setIsVideoPaused,
    setIsVideoMuted,
    switchChannelById,
    setIsFullScreen
  } = useTVChannels();
  const switchNextChannel = () => {
    const next = channels.getNextChannel();
    if (next) switchChannelById(next.id);
  };
  const switchPreviousChannel = () => {
    const next = channels.getPreviousChannel();
    if (next) switchChannelById(next.id);
  };
  
  const handleFullscreenToggle = () => {
    if (!document.fullscreenElement) {
      if (document.documentElement.requestFullscreen) {
        document.documentElement.requestFullscreen().then(() => {
          setIsFullScreen(true);
        });
      }
    } else {
      if (document.exitFullscreen) {
        document.exitFullscreen().then(() => {
          setIsFullScreen(false);
        });
      }
    }
  };
  useEffect(() => {
    const onFullScreenChange = () => {
      setIsFullScreen(!!document.fullscreenElement);
    };

    document.addEventListener("fullscreenchange", onFullScreenChange);

    return () => {
      document.removeEventListener("fullscreenchange", onFullScreenChange);
    };
  }, [setIsFullScreen]);
  
  if (isChannelsListOpen || isUserInactive) return <></>;
  
  return (
    <Stack
      direction="column"
      sx={{
        position: "fixed",
        // top: sm ? "calc(42vh)" : xs ? "calc(15vh)" : "calc(15vh)",
        paddingTop: sm ? "calc(50vh - 175px)" : xs ? "calc(5vh)" : "calc(5vh)",
        top: 0,
        right: 0,
        zIndex: 10,
        background:
          "linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.7) 100%)",
        // backdropFilter: "blur(1px)",
        height: "100%",
      }}
    >
      <IconButton
        size="large"
        onClick={() => {
          setIsChannelsListOpen(!isChannelsListOpen);
        }}
      >
        <LiveTv />
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          setIsVROpen(!isVROpen);
        }}
      >
        <ViewModule />
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          setIsEPGOpen(!isEPGOpen);
        }}
      >
        <Dvr />
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          setIsVideoPaused(!isVideoPaused);
        }}
      >
        {!isVideoPaused ? <Pause />:<PlayArrow />}
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          switchNextChannel();
        }}
      >
        <SkipNext />
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          switchPreviousChannel();
        }}
      >
        <SkipPrevious />
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          setIsVideoMuted(!isVideoMuted);
        }}
      >
        {isVideoMuted ? <VolumeOff/>:<VolumeUp/>}
      </IconButton>
      <IconButton
        size="large"
        onClick={() => {
          handleFullscreenToggle();
        }}
      >
        <Fullscreen />
      </IconButton>
    </Stack>
  );
};

export default Navigation;
